<template>
    <div class="home-wrapper">
        <menu-tabs></menu-tabs>
        <div class="content-wrapper" ref="contentWrapper">
            <!-- 轮播banner组件 -->
<!--             <carousel
                v-if="bannerList.length > 0"
                :interval=3500
                :list="bannerList">
            </carousel> -->
            <!-- 列表部分list组件 -->
            <home-news-list
                :newsList='newsList'
                :lastListLen="lastListLen"
                :needTransition="!listFromCache">
            </home-news-list>
            <b-loading :show="showLoading"></b-loading>
            <!-- 收藏夹组件 -->
            <infinite-loading v-if="!showLoading"
                spinner="spiral"
                :on-infinite="getMoreNews"
                ref="infiniteLoading">
                <span slot="no-more">
                  亲，已经拉到底啦
                </span>
            </infinite-loading>
        </div>
        <news-favor-list
            :list='newsFavorList' :show="newsFavorListShow"
            @hide-favorList="hideFavorList">
        </news-favor-list>
        <preview
            :show="preview.show"
            :imageList="preview.images"
            :index="preview.index"
            @click-close="closePreview">
        </preview>
    </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex';
import InfiniteLoading from 'vue-infinite-loading';
import MenuTabs from '@/components/MenuTabs.vue';
import Carousel from '@/components/Carousel.vue';
import HomeNewsList from '@/components/HomeNewsList.vue';
import NewsFavorList from '@/components/NewsFavorList.vue';
import EventBus from '@/event-bus';
import Preview from '@/components/Preview.vue';
import BLoading from '@/components/BLoading.vue';

export default {
    name: 'home',
    props: {},
    data() {
        return {
            newsFavorListShow: false,
            scrollTops: {},
            showLoading: true
        }
    },
    components: {
        HomeNewsList,
        InfiniteLoading,
        MenuTabs,
        Carousel,
        NewsFavorList,
        Preview,
        BLoading
    },
    methods: {
        ...mapActions('appShell/appHeader', [
            'setAppHeader'
        ]),
        ...mapActions('appShell/appBottomNavigator', [
            'showBottomNav',
            'activateBottomNav'
        ]),
        ...mapActions([
            'getNewsList',
            'getNewsFavorList',
            'closePreview'
        ]),
        async getMoreNews() {
            await this.getNewsList(this.category);
            setTimeout(() => this.$refs.infiniteLoading.$emit('$InfiniteLoading:' + this.loaded), 100);
        },
        hideFavorList() {
            this.newsFavorListShow = false;
        }
    },
    computed: {
        ...mapGetters([
            'category',
            'listFromCache',
            'loaded',
            'data',
            'lastListLen',
            'menuTabs',
            'newsFavorList',
            'preview'
        ]),
        newsList() {
            if (!this.data[this.category]) {
                this.showLoading = true;
                return [];
            }

            this.showLoading = false;
            return this.data[this.category].news;
        }
    },
    watch: {
        category(val, old) {
            console.log(this.listFromCache)
            this.scrollTops[old] = this.$refs.contentWrapper.scrollTop;
        }
    },
    updated(data) {
        if (this.listFromCache) {
            this.$refs.contentWrapper.scrollTop = this.scrollTops[this.category];
        }
    },
    async asyncData({store, route}) {
        let category = route.params.category || 'remen';
        await store.dispatch('selectTab', category);
        store.dispatch('getNewsFavorList');
    },
    async activated() {
        this.setAppHeader({
            show: true,
            title: '百度新闻',
            showFavor: true,
            showBack: false,
            showLogo: false,
            actions: [
                {
                    icon: 'search',
                    route: '/search'
                }
            ]
        });
    },
    created() {
        EventBus.$on('app-header:click-favor', () => {
            this.newsFavorListShow = true;
        });
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom


.carousel
    height 232px
    width 100%
.content-wrapper
    position: absolute
    left: 0
    right: 0
    bottom: 0
    top: 40px
    -webkit-overflow-scrolling: touch
    overflow-x: hidden
    overflow-y: auto

// .loading-spiral
//     border-color: $theme.primary !important

</style>



